@font-face {
  font-family: 'icomoon';
  src:  url(./speed/src/assets/images/icomoon.eot);
  src:  url(./speed/src/assets/images/icomoon.eot#iefix) format('embedded-opentype'),
  url(./speed/src/assets/images/icomoon.ttf) format('truetype'),
  url(./speed/src/assets/images/icomoon.woff) format('woff'),
  url(./speed/src/assets/images/icomoon.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-menu:before {
  content: "\e9bd";
}

body {
  font-family: 'Source Sans Pro', system-ui;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6{
  font-family: 'Fjalla One', system-ui;
  font-weight: normal;
}

h1 {
  font-size: 40px;
  max-width: 500px;
  line-height: 1.5;
  letter-spacing: -.2px;
  color: white;
  margin: 0;
}

h1 strong {
  color: #026fff;
}

h2 {
  color: #026fff;
  text-transform: uppercase;
  font-family: 'Source Sans Pro', system-ui;
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 40px;
}

.hero {
  height: 300px;
  background-color: #1b2127;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.hero-image{
  object-fit: cover;
}

.portfolio {
  background-color: #fafafa;
  padding: 20px;
}

.project {
  border: 1px solid gray;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 20px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 3px 5px 33px#888888;
  flex-wrap: wrap;
}

.project-course,
.project-date,
.project-url {
  margin: 10px 0;
}

.project-description {
  font-size: 20px;
}

.project strong {
  font-family: 'Fjalla One', system-ui;
}

.project-title {
  font-size: 30px;
  /* margin-top: 0;
  margin-bottom: 10px; */
  margin: 0 0 10px 0;
}

.project-details {
  width: 50%;
}

.project-imageContainer {
  width: 50%;
}

.project-imageContainer img{
  max-width: 100%;
}

.header {
  background-color: #1b2127;
  color: white;
  display: flex;
  flex-wrap: wrap;
  height: 70px;
  align-items: center;
  justify-content: space-between;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

figure {
  margin: 0;
}

.menu {
  height: inherit;
}

.header ol {
  display: flex;
  height: inherit;
}

.header ol li {
  height: inherit;
}

.header a {
  color: white;
  text-decoration: none;
  height: inherit;
  display: flex;
  align-items: center;
  padding: 0 10px;
}

.footer {
  color: white;
  background-color: #1b2127;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.footer img {
  vertical-align: middle;
}

.footer a {
  color: white;
}

.event-list {
  display: flex;
  flex-wrap: wrap;
}
.event-list-title {
  margin-left: 20px;
}
.event {
  margin: 1%;
  width: 48%;
  flex-shrink: 0;
  border-radius: 10px;
  overflow: hidden;
  background: #f4fbff;
}
.event img {
  width: 480px;
  height: 200px;
  object-fit: cover;
}
.event-detail {
  margin: 20px 40px;
  margin-top: -40px;
  background-color: white;
  position: relative;
  padding: 20px;
  text-align: center;
}
.event-description {
  text-align: left;
}

.event-url {
  color: #056fff;
  border: 1px solid;
  padding: 5px 20px;
  text-decoration: none;
  border-radius: 5px;
}

.contact {
  background-color: #056fff;
  color: white;
  height: 150px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.social-link {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 0 10px;
  background-size: 50px 50px;
}

.social-link.twitter.lazy {
  background-image: none;
  background-color: blue;
}
.social-link.facebook.lazy {
  background-image: none;
  background-color: blue;
}
.social-link.github.lazy {
  background-image: none;
  background-color: blue;
}
.social-link.instagram.lazy {
  background-image: none;
  background-color: blue;
}

.form-email.lazy input {
  border-color: gray;
  border-style: solid;
  border-radius: 5px;
  padding: 10px 20px 10px 25px;
  background-image: none;
  background-size: 15px 15px;
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 5px;
}

.social-link.twitter {
  background-image: url(./speed/src/assets/images/twitter.svg);
}
.social-link.facebook {
  background-image: url(./speed/src/assets/images/facebook.svg);
}
.social-link.github {
  background-image: url(./speed/src/assets/images/github.svg);
}
.social-link.instagram {
  background-image: url(./speed/src/assets/images/instagram.svg);
}


.form-email input {
  border-color: gray;
  border-style: solid;
  border-radius: 5px;
  padding: 10px 20px 10px 25px;
  background-image: url(./speed/src/assets/images/envelope.svg);
  background-size: 15px 15px;
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 5px;
}

.form-email button {
  display: block;
  background: transparent;
  color: white;
  padding: 10px 0;
  border-radius: 5px;
  width: 100px;
  margin-top: 10px;
}

.form-email h3 {
  font-family: 'Source Sans Pro', system-ui;
}

.container {
  /* border: 1px solid red; */
  max-width: 1000px;
  margin: 0 auto;
  display: inherit;
  justify-content: inherit;
  align-items: inherit;
  height: inherit;
  flex-wrap: inherit;
  flex:1;
}

.html-video{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  right: 0;
  bottom: 0;
  left: 0;
}

.youtube-video{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  right: 0;
  bottom: 0;
  left: 0;
}

.flexible-video{
  width: 100%;
  height: 0;
  padding-top: 56.25%;
  position: relative;
}

.burger-button{
  display:none;
  color: white;
  position: fixed;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 50%;
  background-color: rgba(0,0,0,0.8);
  z-index: 4;
  left: 5px;
  top:5px;
}